@keyframes pulse {
  0% {
      transform: scale(1); /* Initial size */
  }
  50% {
      transform: scale(1.1); /* Scale up */
  }
  100% {
      transform: scale(1); /* Back to original size */
  }
}

/* Apply animation to button on hover */
.nightmode:hover {
  animation: pulse 0.5s infinite alternate; /* Apply animation */
}

