/* Skill start */



.skill{
  padding: 6rem 6rem 6rem;
  background: var(--body-color);
}



.skills{
  background: var(--secon-bg-color);
}



.skill-bar{
  padding: 40px;
}




.skill-main{
  width: 100%;
  display:grid;
  grid-template-columns: repeat(2,1fr);
  grid-row-gap: 30px;
  grid-column-gap: 50px;
}


.skill-bar{
  margin-bottom: 2.3rem;
}

.skill-main {
  margin-bottom: 2rem;
  font-size: var(--normal-font);
  text-align: center;
}


.skill-left .skill-bar .info{
  display:flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
}



.skill-left .skill-bar .info p{
  color: var(--other-clr);
}


.skill-left .skill-bar .bar{
  width:100%;
  height: 10px;
  background-color: var(--bg-color);
  border-radius:25px;
  margin-top:10px;
  position: relative;
}


.skill-bar .bar span{
  width: 50%;
  height:100%;
  position: absolute;
  left: 0;
  background: var(--hover-color);
  border-radius: 25px;
  box-shadow: var(--neon-box-shadow);
}


.skill-bar .bar .html{
  width: 80%;
  animation: html 2s;
}


.skill-bar .bar .css{
  width: 70%;
  animation: css 3s;
}


.skill-bar .bar .javaScript{
  width: 80%;
  animation: javaScript 4s;
}


.skill-bar .bar .reactjs{
  width: 70%;
  animation: reactjs 5s;
}


.skill-bar .bar .nodejs{
  width: 70%;
  animation: nodejs 6s;
}


.skill-bar .bar .mongodb{
  width: 75%;
  animation: mongodb 7s;
}


/* keyframe of skills   */
@keyframes html{
  0%{
    width: 0%;
  }
  100%{
    width: 80%;
  }
}

@keyframes css{
  0%{
    width: 0%;
  }
  100%{
    width: 70%;
  }
}

@keyframes javaScript{
  0%{
    width: 0%;
  }
  100%{
    width: 80%;
  }
}

@keyframes reactjs{
  0%{
    width: 0%;
  }
  100%{
    width: 70%;
  }
}

@keyframes nodejs{
  0%{
    width: 0%;
  }
  100%{
    width: 70%;
  }
}


@keyframes mongodb{
  0%{
    width: 0%;
  }
  100%{
    width: 75%;
  }
}


hr { 
  width:100%; 
  height:1px;
  color: var(--para-clr);
