  position: relative;
  overflow: hidden;
 
}



.other-skill i{
  
  animation: moveAndRotate 8s linear infinite alternate;
 
}





/* Animation for translating left */
@keyframes moveAndRotate {
  0% {
    transform: translateX(0) rotate(0deg);
  }
  25% {
    transform: translateX(50%) rotate(10deg);
  }
  50% {
    transform: translateX(0) rotate(0deg);
  }
  75% {
    transform: translateX(-50%) rotate(-10deg);
  }
  100% {
    transform: translateX(0) rotate(0deg);
  }
}

