.btn-box{
  display:flex;
  justify-content: space-between;
  width: 320px;
  height: 45px;
  justify-content: center;
  align-items: center;

}


.btn-box .btn{
  
  display:inline-flex;
  justify-content: center;
  align-items: center;
  width: 320px;
  height: 45px;
  background: var(--hover-color);
  color: var(--bg-color);
  font-size: 1rem;
  letter-spacing: 1px;
  font-weight: 600;
  transition: .6s;
  box-shadow: var(--neon-box-shadow);
  border-radius: 5px;
  position: relative;
  overflow: hidden;
  z-index: 1;
  border: 2px solid var(--hover-color);
}


a.btn:hover{
    color:var(--hover-color);
}

a.btn::before{
  content: "";
  position: absolute;
  top: 0;
  left:0;
  background: var(--bg-color);
  width: 0;
  height: 100%;
  z-index: -1;
  transition: .4s;
}

a.btn:hover::before{
  width:100%;

}

a.btn:nth-child(2){
  background: var(--bg-color);
  color: var(--hover-color);
}

a.btn:nth-child(2):hover{
  color: var(--bg-color);
}

a.btn:nth-child(2)::before{
  background: var(--hover-color);
}


