/* Home image animation start */

.img-box{
  position: relative;
  width: 400px;
  height: 400px;
  background: var(--hover-color);
  border-radius: 50%;
  overflow: hidden;
}

.img-box::before
{
  content: '';
  position:absolute;
  inset: -10px 140px;
  background: var(--body-color);
  transition: 0.5s;
  animation: animate 4s linear infinite;
}

.img-box:hover::before
{
  inset: -20px 0px;
}

@keyframes animate
{
  0%
  {
    transform: rotate(0deg);
  }
  100%
  {
    transform: rotate(360deg);
  }
}


.img-box::after
{
  content:'';
  position:absolute;
  inset: 6px;
  background: var(--body-color);
  border-radius: 50%;
  z-index: 1;
}

.img-content{
  position:absolute;
  inset: 30px;
  border: 6px solid var(--default-clr);
  z-index: 3;
  border-radius: 50%;
  overflow: hidden;
  display:flex;
  justify-content:center;
  align-items:center;
  flex-direction: column;
}

.img-content img{
  position:absolute;
  top:0;
  left:0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 0.5s;
  pointer-events: none;
  z-index: 3;
}


.img-box:hover .img-content img
{
  opacity: 0;
}


.img-content h2
{
  position: relative;
  font-size: 1.5em;
  text-align: center;
  color:var(--hover-color);
  font-weight: 600;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}


.img-content h2 span
{
  font-weight: 300;
  font-size: 0.75em;
}

.img-content a
{
  position: relative;
  margin-top: 10px;
  padding: 10px 20px;
  background: var(--secon-bg-color);
  color:#1c1e1d;
  border-radius:25px;
  font-weight: 500;
  font-size: 1.25em;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  text-decoration: none;
  transition: 0.5s;
}

.img-content a:hover{
  letter-spacing: 0.2em;
}









