/* others */

/* Project start */
 
h1{
  color: #076f6f;
}
*{
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif; 
  box-sizing: border-box;
}
body{
background: #1e1f21;
  color: #fff;
}
.wrapper{
  padding: 10px 10%;
}
#card-area{
  padding: 50px 0;
  background-color: var(--body-color);
}
.box-area{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 40px;
  margin-top: 50px;
}
.box {
border-radius: 10px;
position: relative;
overflow: hidden;
box-shadow: 5px 5px 15px rgba(0,0,0,0.9);
}
.box img{
  width: 100%;
  border-radius: 10px;
  display: block;
  transition: transform 0.5s;
}
.overlay {
width: 100%;
height: 0;
background: linear-gradient(transparent,#1c1c1c 58%);
border-radius: 10px;
position: absolute;
top:0;
left: 0;
bottom: 0;
overflow: hidden;
display: flex;
align-items: center;
justify-content: center;
flex-direction: column;
padding: 0 100px;
text-align: center;
font-size: 14px;
transition: height 0.5s;
}
.overlay h3 {
font-weight: 500;
margin-bottom: 5px;
margin-top: 80%;
font-family: 'Bebas Neue', sans-serif;
font-size: 30px;
letter-spacing: 2px;
}
.overlay a {
margin-top: 10px;
color: #262626;
text-decoration: none;
font-size: 14px;
background: #fff;
border-radius: 50px;
text-align: center;
padding: 5px 15px;
}
.box:hover img{
  transform: scale(1.1);
}
.box:hover .overlay{
  height: 100%;
}


@media (min-width: 300px) and (max-width: 700px) {
  /* Styles for devices between 300px and 700px */
 
  .home-content{
      display: flex;
      flex-direction: column-reverse;
  }
  .home-content span{
    font-size: 10px;
  }
  .img-box{
    width: 300px;
    height: 300px;
    right: 2rem;
  }


}

/* others end */